import React from 'react';
import { createRoot } from 'react-dom/client';
import { MetaMask } from '@nevermined-io/catalog-providers';
import { AssetService, AuthToken, Catalog } from '@nevermined-io/catalog-core';
import { ethers } from 'ethers';
import { PoapWidget } from './poap-widget';
import {
  gatewayUri,
  gatewayAddress,
  faucetUri,
  verbose,
  graphHttpUri,
  marketplaceUri,
  artifactsFolder,
  nodeUri,
  correctNetworkId,
  secretStoreUri,
} from '../../utils/config';
import { chainConfig } from '../../utils/chainConfig';

const appConfig = {
  web3Provider: window.ethereum || new ethers.providers.JsonRpcProvider(),
  nodeUri,
  gatewayUri,
  faucetUri,
  verbose,
  gatewayAddress,
  graphHttpUri,
  marketplaceAuthToken:
    AuthToken.fetchMarketplaceApiTokenFromLocalStorage().token,
  marketplaceUri,
  artifactsFolder,
  secretStoreUri,
};

const element = document.getElementById('poap-widget');

console.log(element);

if (element) {
  const did = element.getAttribute('nvm-did');

  if (did) {
    createRoot(element!).render(
      <Catalog.NeverminedProvider
        config={appConfig}
        verbose={appConfig.verbose}
      >
        <MetaMask.WalletProvider
          externalChainConfig={chainConfig}
          correctNetworkId={correctNetworkId}
          nodeUri={nodeUri}
        >
          <AssetService.AssetPublishProvider>
            <PoapWidget did={did} />
          </AssetService.AssetPublishProvider>
        </MetaMask.WalletProvider>
      </Catalog.NeverminedProvider>
    );
  }
}

export default () => {
  return <></>;
};
