import React, {
  useEffect,
  useState,

} from 'react';
import { Catalog, getCurrentAccount, DDO, Logger, BigNumber } from '@nevermined-io/catalog-core';
import { MetaMask } from '@nevermined-io/catalog-providers';
import {
  MetamaskCustomErrors,
  MetamaskErrorCodes,
} from '../../utils/constants';
import { UiLayout, UiButton, UiText } from '@nevermined-io/styles'

Logger.setLevel(3);

type PoapWidgetProps = {
  did: string;
};

export const PoapWidget: React.FC<PoapWidgetProps> = ({ did }) => {
  const { sdk, account, isLoadingSDK, subscription } = Catalog.useNevermined();
  const { walletAddress, isAvailable, checkIsLogged } = MetaMask.useWallet();
  const [owner, setOwner] = useState('')
  const [ownNFT721, setOwnNFT721] = useState(false)
  const [isBought, setIsBought] = useState(false)

  useEffect(() => {
    (async () => {
      setOwnNFT721(await account.isNFT721Holder(did, walletAddress))
      setOwner(await sdk.assets.owner(did))
    })()
  }, [walletAddress, isBought])

  const reedemAsset = async () => {
    try {
      const currentAccount = await getCurrentAccount(sdk)
      if (!account.isTokenValid()
        || account.getAddressTokenSigner().toLowerCase() !== currentAccount.getId().toLowerCase()
      ) {
        await account.generateToken()
      }

      const response = await subscription.buySubscription(did, currentAccount, owner, BigNumber.from(1), 721)
      setIsBought(Boolean(response))
    } catch (error: any) {
      console.error(
        error.code === MetamaskErrorCodes.CANCELLED
          ? MetamaskCustomErrors.CANCELLED[1]
          : error.message
      );
    } finally {
    }
  };

  return (
    <UiLayout>
      {ownNFT721 ? (
        <UiText>Already redeemed</UiText>
      ) : (
        owner !== walletAddress ?
        <UiButton onClick={reedemAsset} disabled={isLoadingSDK}>
          REEDEM
        </UiButton>
        : <UiText>You are the owner</UiText>
      )}
    </UiLayout>
  );
};
